@import "minimal-course";



$font-family: Montserrat;
$theme-color: rgb(101, 78, 163);

html { font-family: $font-family; }
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6, .heading { font-family: $font-family; }
body { font-family: $font-family;}


main {
  a, a:link, a:visited, a:active{
      color: $theme-color;
  }
  a:hover {color: darken($theme-color, 20%); text-decoration: none;}
}

